.hero-bg{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
}

.hero-bg video{
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    background: #232a34;
}

.hero-flex{
    color: #fff;
    padding: 2rem 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-image img{
    width: 50%;
}

.hero-image{
    width: 50%;
    text-align: center;
    z-index: -1;
}

.hero-btn{
    text-decoration: none;
    color: #fff;
    background: #232a34;
    padding: 25px;
    border-radius: 30px;
    transition: 0.5s ease-in-out;
}

.hero-content h1{
    padding-bottom: 3rem;
}

.hero-btn:hover{
    background: #fff;
    color: #232a34;
    transition: 0.5s ease-in-out;
}

.hero-image{
    background-image: url('../../assets/austro.png');
    width: 50%;
    height: 700px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.hero-image{
    animation: updown 3s linear infinite;
}

@keyframes updown {
    0%{
        transform: translateY(-20px);
    }
    50%{
        transform: translateY(20px);
    }
    100%{
        transform: translateY(-20px);
    }
}

@media screen and (max-width: 1000px) {
    .hero-flex{
        flex-direction: column;
        padding: 6rem 4rem;
        gap: 4rem;
    }

    .hero-content{
        text-align: center;
    }

    .hero-image{
        width: 100%;
        height: 500px;
    }
}

@media screen and (max-width: 500px) {
    .hero-image{
        width: 100%;
        height: 300px;
    }
}