.NavbarItems{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 4rem;
    background: transparent;
  }
  
  .nav-menu{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
  }
  
  .nav-menu li{
    list-style: none;
  }
  
  .nav-menu a{
    text-decoration: none;
    color: #fff;
  }
  
  .navbar-links{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
  }
  
  .navbar-links li{
    list-style: none;
  }
  
  .navbar-links a{
    text-decoration: none;
    color: #fff;
  }
  
  .menu-icon{
    display: none;
  }
  
  .navbar-logo{
    text-transform: uppercase;
    cursor: pointer;
    color: #fff;
  }
  
  .navbar-links-responsive{
    display: none;
  }

  .navbar-links a{
    background: #1C233D;
    padding: 10px;
    border-radius: 30px;
    align-items: center;
    transition: 0.5s ease-in-out;
  }

  .navbar-links a{
    width:40px;
    height:40px;
    background-color:#1C233D;
    display:flex;
    align-items:center;
    justify-content:space-around;
    padding:11px;
    border-radius:51%;
    box-sizing:border-box;
  }

  .navbar-links a:hover{
    background: #fff;
    color: #1C233D;
    transition: 0.5s ease-in-out;
  }

  
  @media screen and (max-width: 1000px) {
    .nav-menu{
      display: none;
    }
  
    .nav-menu.active{
      background: #1C233D;
      width: 100%;
      height: calc(100% - 68px);
      position: fixed;
      top: 68px;
      left: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
  }
  
    .nav-menu.active li{
      text-align: center;
      margin-left: 0;
      margin-top: 4rem;
    }
  
    .menu-icon{
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 65%);
      font-size: 1.8rem;
      cursor: pointer;
      color: #fff;
    }
  
    .NavbarItems{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      padding: 40px 100px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      transition: 0.5s;
      background: #29434e;
    }
  
    .nav-menu.active{
      background: #29434e;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .navbar-logo{
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 75%);
    }
  
    .navbar-links{
      display: none;
    }
  
    .navbar-links-responsive{
      display: unset;
    }
  }