.about-container{
    padding: 5rem 4rem;
    background: #1C233D;
    border-radius: 30px 30px 0 0;
    color: #fff;
}

.about-container-header{
    text-align: center;
    color: #fff;
}

.about-container-flex-image{
    text-align: center;
}

.about-container-flex-image img{
    width: 50%;
    border-radius: 50%;
}

.about-container-flex{
    display: flex;
    align-items: center;
}

.about-container-flex-reverse{
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
}

.about-links a{
    background: #1C233D;
    padding: 10px;
    border-radius: 30px;
    align-items: center;
    transition: 0.5s ease-in-out;
  }

  .about-links a{
    width:40px;
    height:40px;
    background-color:#232a34;
    display:flex;
    align-items:center;
    justify-content:space-around;
    padding:11px;
    border-radius:51%;
    box-sizing:border-box;
    color: #fff;
  }

  .about-links a:hover{
    background: #fff;
    color: #1C233D;
    transition: 0.5s ease-in-out;
  }

  .about-links li{
    list-style: none;
  }

  .about-links{
    display: flex;
    justify-content: center;
    gap: 1rem;
  }

  @media screen and (max-width: 1000px) {
    .about-container-flex, .about-container-flex-reverse{
        flex-direction: column;
        padding-top: 2rem;
    }

    .about-links{
        padding-top: 2rem;
    }
  }